*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --col-bg-primary: #fff;
  --col-bg-light: #fcfcfc;
  --col-bg-middle: #97af8d;
  --col-bg-dark: #84a178;
  --col-bg-cream: #aec6a5;
  --col-bg-cofee: #efd478;
  --col-bg-milkshakes: #d6936d;
  --col-text-primary: #131313;
  --col-text-secondary: #fcfcfc;
  --col-title-primary: #131313;
  --col-title-secondary: #fcfcfc;
  --col-default: #f8d24e;
  --col-accent: #0a8817;
  --col-btn-default: #000;
  --col-btn-hover: #000;
  --col-btn-focus: #fcfcfc;
  --col-btn-disabled: #000;
  --col-menu-default: #f8d24e;
  --col-menu-hover: #0a8817;
  --col-menu-focus: #fcfcfc;
  --col-menu-disabled: #ababab;
  --col-btn-bg-default: #f8d24e;
  --col-btn-bg-hover: #0a8817;
  --col-btn-bg-focus: #0a8817;
  --col-btn-bg-disabled: #ababab;
  --col-icon-light: #fcfcfc;
  --col-icon-dark: #84a178;
  --col-icon-bg: #f8d24e;
  --col-icon-bg-dark: #84a178;
  --col-backdrop: rgba(0, 0, 0, .2);
  --col-border-form: #f8d24e;
  --border-btn-transparent: 2px solid #0a8817;
  --border-input: 1px solid #84a178;
  --cubic-function: cubic-bezier(.4, 0, .2, 1);
  --animation-time: 250ms;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  width: 100%;
  display: block;
}

.link {
  color: inherit;
  text-decoration: none;
}

.address {
  font-style: normal;
}

:focus {
  outline-style: none;
}

body {
  letter-spacing: .04em;
  color: var(--col-text-primary);
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  line-height: 1.76;
}

a {
  cursor: url("cursor.839906a3.png"), pointer;
}

.section {
  background-color: var(--col-bg-dark);
  padding-bottom: 60px;
}

.container {
  max-width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.no-scroll {
  overflow: hidden;
}

.hide {
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  width: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  top: 20px;
  left: 28px;
  overflow: hidden;
  word-wrap: normal !important;
}

@keyframes slide-top {
  from {
    transform: scale(.9);
  }

  to {
    transform: scale(1.1);
  }
}

@keyframes scaler {
  from {
    transform: scale(.8);
  }

  to {
    transform: scale(1);
  }
}

.scroll-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--col-btn-bg-default);
  cursor: pointer;
  z-index: 999;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  animation: slide-top .75s linear, scaler 1.5s infinite alternate both;
  display: none;
  position: fixed;
  bottom: 5vh;
  right: 30px;
}

.arrow-top {
  fill: #fff;
}

.soc-list {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.soc-list__item {
  width: 44px;
  height: 44px;
}

.soc-list__link, .soc-list__link--dark {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn {
  cursor: url("cursor.839906a3.png"), pointer;
  color: var(--col-btn-default);
  background-color: var(--col-btn-bg-default);
  transition: color var(--animation-time) var(--cubic-function), background-color var(--animation-time) var(--cubic-function);
  border-style: none;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.btn:hover {
  color: var(--col-btn-hover);
  background-color: var(--col-btn-bg-hover);
}

.btn:focus {
  color: var(--col-btn-focus);
  background-color: var(--col-btn-bg-focus);
  fill: var(--col-btn-focus);
}

.btn--transparent {
  height: 42px;
  transition: color var(--animation-time) var(--cubic-function), border var(--animation-time) var(--cubic-function);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 12px 34px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.28571;
}

.btn--transparent:hover {
  color: var(--col-btn-hover);
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #0a8817;
}

.btn--transparent:focus {
  color: var(--col-btn-focus);
  fill: var(--col-btn-focus);
  background-color: rgba(0, 0, 0, 0);
}

.section-title {
  text-align: center;
  color: var(--col-title-secondary);
  letter-spacing: .04em;
  margin-bottom: 32px;
  font-family: Seymour One, sans-serif;
  font-size: 30px;
  line-height: 1.26;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: var(--col-backdrop);
  z-index: 9999;
  position: fixed;
  top: 0;
}

.modal {
  width: calc(100% - 30px);
  min-height: 580px;
  background: var(--col-bg-light);
  border-radius: 4px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
  .modal {
    max-width: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    max-width: 530px;
  }
}

.modal .backdrop.is-hidden {
  transform: translate(-50%, -50%)scale(.5);
}

.modal-close {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-title {
  text-align: center;
  color: title-color;
  font-size: 20px;
  font-weight: 700;
}

.modal-form {
  flex-direction: column;
  display: flex;
}

.modal-field {
  margin-bottom: 5px;
}

.input-wrapper {
  position: relative;
}

.modal-icon {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.input-label {
  letter-spacing: .01em;
  font-size: 12px;
}

.modal-input {
  width: 100%;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid;
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  padding-left: 42px;
  transition: border-color;
}

.modal-coment {
  width: 100%;
  height: 120px;
  border: 1px solid var(--col-backdrop);
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
  outline: rgba(0, 0, 0, 0);
  margin-bottom: 5px;
  padding-top: 12px;
  padding-left: 15px;
}

.check-text {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .check-text {
    font-size: 14px;
  }
}

.check-wrap {
  display: flex;
}

.check-text span {
  width: 16px;
  height: 15px;
  fill: rgba(0, 0, 0, 0);
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  margin-right: 7px;
  display: flex;
}

.modal-check:checked + .check-text span {
  border: none;
}

.modal-check:hover + .check-text span {
  cursor: pointer;
}

.modal-check:focus + .check-text span {
  border-color: var(--col-backdrop);
}

.modal-field {
  position: relative;
}

.agreement-link {
  cursor: pointer;
  padding-left: .5em;
  text-decoration: underline;
}

.menu-toggle {
  min-height: 40px;
  min-width: 40px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  position: absolute;
  top: 10px;
  right: 15px;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: var(--col-bg-light);
  padding: 25px 45px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-wrap {
  height: calc(100% - 170px);
  flex-direction: column;
  justify-content: space-between;
  gap: 15%;
  display: flex;
}

.mobile-menu__item {
  padding: 15px 0;
}

.mobile-menu__link {
  letter-spacing: .02em;
  font-size: 40px;
  font-weight: 500;
  line-height: 1.17;
}

.contact-menu__item {
  padding: 15px 0;
}

.contact-menu__link--tel {
  letter-spacing: .02em;
  font-size: 34px;
  font-weight: 500;
  line-height: 1.17;
}

.contact-menu__link--mail {
  letter-spacing: .02em;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
}

.menu-soc-list {
  gap: 20px;
  display: flex;
}

.menu-soc-list__item {
  align-items: center;
  display: flex;
  position: relative;
}

.menu-soc-list__link {
  letter-spacing: .02em;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22;
}

.menu-soc-list__link:before {
  width: 1px;
  height: 100%;
  content: "";
  border-radius: 2px;
  position: absolute;
  bottom: 2px;
  left: -10px;
}

.header {
  width: 100%;
  height: 53px;
  z-index: 4;
  background-color: var(--col-bg-dark);
  position: fixed;
  top: 0;
}

@media screen and (min-width: 768px) {
  .header {
    height: 66px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    height: 76px;
  }
}

.header__button {
  display: none;
}

@media screen and (min-width: 768px) {
  .header__button {
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.header__button--mobile {
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__button--mobile {
    display: none;
  }
}

.header__button-img {
  fill: currentColor;
  margin-left: 4px;
}

.header--container {
  align-items: center;
  padding-top: 12px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header--container {
    padding-top: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .header--container {
    justify-content: space-between;
    align-items: flex-end;
    padding-top: 8px;
  }
}

.header-nav {
  display: none;
}

@media screen and (min-width: 1200px) {
  .header-nav {
    margin-left: 90px;
    display: flex;
  }
}

.header-nav__list {
  height: 42px;
  align-items: center;
  display: flex;
}

.header-nav__link {
  letter-spacing: 0;
  color: var(--col-title-secondary);
  transition: color var(--animation-time) var(--cubic-function);
  border-radius: 30px;
  padding: 10px 5px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.header-nav__link:focus, .header-nav__link:hover {
  color: var(--col-menu-default);
  transform: scale(1.1);
  box-shadow: 0 4px 10px rgba(153, 114, 23, .25);
}

.header-nav__item:not(:last-child) {
  margin-right: 16px;
}

.header__mobile-close, .header__mobile-open {
  color: var(--col-bg-primary);
  cursor: pointer;
  transition: color var(--animation-time) var(--cubic-function);
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  align-items: center;
  margin-left: auto;
  padding: 0;
  display: flex;
}

.header__mobile-close:focus, .header__mobile-open:focus {
  color: var(--col-menu-default);
}

.header__mobile-icon {
  fill: currentColor;
}

@media screen and (min-width: 768px) {
  .header__mobile-open {
    margin-right: 36px;
  }
}

@media screen and (min-width: 1200px) {
  .header__mobile-open {
    display: none;
  }
}

.header__mobile-icon {
  fill: currentColor;
}

.header__mobile-menu {
  width: 100vw;
  height: 100vh;
  background-color: var(--col-bg-dark);
  z-index: 5;
  transition: transform var(--animation-time) var(--cubic-function);
  padding: 12px 20px 64px;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .header__mobile-menu {
    padding: 20px 32px 0;
  }
}

@media screen and (min-width: 1200px) {
  .header__mobile-menu {
    display: none;
  }
}

.header__mobile-menu.is-open {
  transform: translateX(0);
}

.header__mobile-wrapper {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 51px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__mobile-wrapper {
    margin-bottom: 76px;
  }
}

.header__nav-mobile {
  margin-bottom: 110px;
}

.header__nav-mobile-list {
  text-align: center;
}

.header__nav-mobile-link {
  text-align: center;
  color: var(--col-bg-light);
  transition: color var(--animation-time) var(--cubic-function);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.22;
}

@media screen and (min-width: 768px) {
  .header__nav-mobile-link {
    font-size: 48px;
    line-height: 1.23;
  }
}

.header__nav-mobile-link:focus {
  color: var(--col-menu-default);
}

.header__nav-mobile-item:not(:last-child) {
  margin-bottom: 32px;
}

@media screen and (min-width: 768px) {
  .header__nav-mobile-item:not(:last-child) {
    margin-bottom: 40px;
  }
}

.mobile-link-current {
  color: var(--col-text-primary);
}

.hero {
  background-color: var(--col-bg-dark);
  padding-top: 118px;
  position: relative;
}

.hero__image-icecream {
  width: 456px;
  z-index: 3;
  position: absolute;
  bottom: 0;
  right: 92px;
}

.hero__point-left {
  width: 171px;
  z-index: 2;
  position: absolute;
  bottom: 17px;
  left: 0;
}

.hero__point-left--mobile {
  display: none;
}

.hero__point-middle {
  width: 492px;
  z-index: 2;
  position: absolute;
  bottom: 46px;
  left: calc(50% - 132px);
  transform: translate(-50%);
}

.hero__point-right {
  width: 285px;
  z-index: 2;
  position: absolute;
  bottom: 0;
  right: 0;
}

.hero__box {
  z-index: 5;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-top: 42px;
  display: flex;
  position: relative;
}

.hero__text {
  margin-top: 30px;
}

.wrap_hero {
  justify-content: space-between;
  padding-top: 14px;
  padding-bottom: 66px;
  display: flex;
  position: relative;
}

.hero_points {
  min-width: 320px;
  z-index: 2;
  display: none;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.hero_img_icecream {
  width: 177px;
  z-index: 3;
  margin-left: 62px;
  position: relative;
}

.hero_tittle {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--col-title-secondary);
  margin-bottom: 28px;
  font-family: Seymour One;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 33px;
}

.hero_btn_first {
  border: 2px solid var(--col-btn-bg-default);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 11px 26px;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 15px;
  display: flex;
}

.hero_btn_first a {
  text-align: center;
  color: var(--col-btn-disabled);
  align-items: center;
  text-decoration: none;
}

.hero_btn_second {
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  align-items: center;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 15px;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  display: flex;
}

.hero_btn_second a {
  color: var(--col-default);
}

.wrap_btn_hero {
  gap: 12px;
  display: flex;
}

.hero_p {
  width: 100%;
  max-width: 216px;
}

.hero_img_plate {
  width: 225px;
}

.hero_numbers {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.hero_number, .hero_cafes, .hero_arrow {
  display: none;
}

@media screen and (max-width: 1199px) {
  .hero {
    padding-top: 80px;
  }

  .hero__image-icecream {
    width: 268px;
  }

  .hero__point-left {
    width: 110px;
    bottom: 4px;
    left: -32px;
  }

  .hero__point-middle {
    display: none;
  }

  .hero__point-right {
    width: 182px;
  }

  .hero__box {
    gap: 32px;
    margin-top: 12px;
  }

  .wrap_hero {
    padding-bottom: 12px;
  }

  .hero_p {
    max-width: 150px;
  }

  .hero_numbers {
    gap: 20px;
  }

  .hero_tittle {
    max-width: 247px;
    margin-bottom: 24px;
    padding-top: 0;
    line-height: 33px;
